* {
  padding: 0;
  margin: 0;
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
}

html {
  height: 100%;
}

body {
  font-family: "arial";
  min-height: 100%;
  background-image: url("./../imgs/crissxcross.png");
  position: relative;
}

/* Styles for bar chart */
.container {
  padding-top: 30px;
  position: relative;
}
.container .shadow {
  background-color: white;
  margin: auto;
  -webkit-box-shadow: 0px 0px 10px #343635;
          box-shadow: 0px 0px 10px #343635;
  width: 1000px;
  min-height: 500px;
}
.container .shadow p {
  text-align: center;
  padding: 20px 50px;
  font-size: 14px;
  color: #0b453e;
}
.container .tooltip {
  border-radius: 3px;
  color: white;
  background-color: #060d18;
  padding: 5px;
  font-size: 12px;
  position: absolute;
  pointer-events: none;
}
.container .tooltip .money {
  font-size: 14px;
  font-weight: 600;
}



